.site-header {
  backface-visibility: hidden;
  z-index: 80;
}
.navbar {
  align-items: center;
  display: flex;
  height: 100px;
  justify-content: space-between;
}
.navbar-left,
.navbar-right {
  align-items: center;
  display: flex;
}
.navbar-left {
  height: 100%;
}
.logo {
  display: inline-block;
  margin-right: 15px;
  position: relative;
  vertical-align: middle;
}
.logo-image {
  max-height: 60px;
}
.logo-image.white {
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity 0.3s var(--animation-base);
}
.logo-text {
  font-size: 24px;
  font-weight: 800;
}
.sep {
  display: flex;
  height: 100%;
  margin: 0 15px;
  width: 1px;
}
.sep:after {
  background-color: var(--mid-gray-color);
  content: "";
  height: 40%;
  margin: auto;
  transition: background-color 0.5s var(--animation-base);
  width: 100%;
}
.nav-list {
  display: flex;
}
.menu-item {
  backface-visibility: hidden;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 0.5px;
  line-height: 1;
  margin: 0 20px;
  transition: opacity 0.3s var(--animation-base);
}
.menu-item-link {
  color: var(--secondary-text-color);
}
.menu-item-link:hover {
  color: var(--dark-gray-color);
}
@media (max-width: 767px) {
  .logo {
    margin-right: 0;
  }
}
